import React, { Component } from 'react';
import PageTemplate2 from 'components/common/PageTemplate2';
import { Helmet } from "react-helmet";
import styles from './page.scss';
import classNames from 'classnames/bind';
//import scrollToComponent from 'react-scroll-to-component';
// import Adsense970250 from 'components/adsense/Adsense970250';
//import Adsense300250 from 'components/adsense/Adsense300250';
import WowItemListContainer from 'containers/wowitem/WowItemListContainer';
//import AccessoryListContainer from 'containers/list/AccessoryListContainer';

class WowItemPage extends Component {
  render() {
    const cx = classNames.bind(styles);

    return (
      <PageTemplate2>
        <Helmet>
          <title> 2 BIS  </title>
          <meta name="description" content="World of Warcraft (WOW)  2 ,   2  . . " />
        </Helmet>
        <WowItemListContainer/>
      </PageTemplate2>
    );
  }
}

export default WowItemPage;